import React, { useEffect, useState ,memo} from 'react'
import "./Products.css";
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import {  useParams } from 'react-router-dom';
import { clearErrors, getProduct } from '../../../redux/actions/productAction';
import Loader from '../../layout/Loader/Loader';
import Product from '../Product/Product';
import Pagination from "react-js-pagination";
import Typography from "@material-ui/core/Typography"
import { Button, Slider } from '@material-ui/core';
import H2 from '../../ReusableComponent/Heading/H2/H2';

const categories = [
    "Laptop",
    "Footwear",
    "Bottom",
    "Top",
    "Attire",
    "Camera",
    "SmartPhone",
    "Electronics",
    "Smart Watches "
    
]

const Products = ({ title }) => {
    const dispatch = useDispatch();
    const alert = useAlert();
   

    const [currentPage, setCurrentPage] = useState(1);
    const [price, setPrice] = useState([1, 100000]);
    const [category, setCategory] = useState("");
    const [ratings, setRatings] = useState(0);
    const [keyword, setKeyword] = useState(useParams().keyword || "");

    const { products, loading, error, productsCount, resultPerPage, filteredProductsCount } = useSelector(state => state.products);

    const setCurrentPageNo = (e = 1) => {
        setCurrentPage(e);
    }

    const setPriceHandler = (e, newPrice) => {
        setCurrentPageNo(1)
        setPrice(newPrice);
    }
    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        dispatch(getProduct(keyword, currentPage, price, category , ratings));
    }, [dispatch, keyword, currentPage, price, alert, error, category,ratings]);

    return (
        <div className='productsContainer'>
            <H2 className="homeHeading">{title}</H2>

<div className='filterBox'>
                <Button onClick={()=>{
                    setCategory("");
                    setRatings(0);
                    setPrice([1, 100000]);
                    setKeyword("");
                }}>All Products</Button>
                <Typography>Price</Typography>
                <Slider
                    value={price}
                    onChange={setPriceHandler}
                    valueLabelDisplay="auto"
                    min={0}
                    max={40000}
                />
                <Typography>Categories</Typography>
                <ul>
                    {categories.map(category =>
                        <li
                            className='category-link'
                            key={category}
                            onClick={() => setCategory(category)}
                        >
                            {category}
                        </li>)}
                </ul>

                <fieldset>
                    <Typography component="legend">Ratings</Typography>
                    <Slider
                        value={ratings}
                        onChange={(e, newRatings) => setRatings(newRatings)}
                        min={0}
                        max={5}
                        valueLabelDisplay="auto"
                    />


                </fieldset>

            </div>
            {loading ? <Loader /> : (<div id="container">

                <div className="products" >
                    {products && products.length > 0 ? products.map(product => <Product key={product._id} product={product} />):<Typography className='noProduct'>No Products Found</Typography>}

                </div>

            </div>)
            }

           

            {resultPerPage < filteredProductsCount && <div className='paginationBox'>
                <Pagination
                    activePage={currentPage}
                    itemsCountPerPage={resultPerPage}
                    totalItemsCount={productsCount}
                    onChange={setCurrentPageNo}
                    nextPageText="Next"
                    prevPageText="Prev"
                    firstPageText="1st"
                    lastPageText="Last"
                    itemClass='page-item'
                    linkClass='page-link'
                    activeClass='pageItemActive'
                    activeLinkClass='pageLinkActive'
                />
            </div>}
        </div>
    )


}

export default memo(Products)